import {IonSpinner,} from '@ionic/react';
import React, {useEffect, useState} from 'react';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import './AdminClienteUpdate.css';

import * as FIcon from "react-feather";
import {Page, SDataTable} from "../../components";
import {RouteComponentProps} from "react-router";
import {useApiToaster} from "../../helpers-ts";


export const AdminApiConfig: React.FC<RouteComponentProps> = observer(({history}) => {
  const rootStore = useStores()
  const [LoadingApikey, setLoadingApikey] = useState(false)
  const [DatosF, setDatosF] = useState<{} | null>(null)
  const DataTable = React.createRef<any>()
  const topRef = React.createRef<HTMLHeadingElement>()
  const bottomRef = React.createRef<HTMLDivElement>()
  const executeScroll = (theref: any) => theref.current?.scrollIntoView()

  const apiToast = useApiToaster()

  useEffect(() => {
    executeScroll(bottomRef)
  }, [bottomRef]);

  const setDato = (campo: string, valor: any) => {
    if (DatosF) {
      let datos = {...DatosF}
      // @ts-ignore
      datos[campo] = valor
      setDatosF(datos)
    }
  }

  const getDato = (campo: string) => {
    if (DatosF) {
      let datos = {...DatosF}
      // @ts-ignore
      return datos[campo]
    } else return ''
  }

  const renderAcciones = (row: any) => {
    return <div>
            <span className={'button ml-3'} title={'Editar'} onClick={() => {
              setDatosF(Object.assign({}, row, {api_key: '-'}));
              executeScroll(bottomRef as any)
            }}><FIcon.Edit
              size={14}/></span>
    </div>
  }


  const verApikey = () => {
    setLoadingApikey(true)
    rootStore.environment.api.accionApi('get-apikey', DatosF).then((result: any) => {
      setLoadingApikey(false)
      if (result.kind === "ok") {
        setDato('api_key', result.response)
      } else {
        apiToast(result, '', 'leer apikey')
      }
    })
  }


  const regenerarApikey = () => {
    setLoadingApikey(true)
    rootStore.environment.api.accionApi('regen-apikey', DatosF).then((result: any) => {
      setLoadingApikey(false)
      apiToast(result, 'Token regenerado', 'regenerar apikey')
      if (result.kind === "ok") {
        setDato('api_key', result.response)
      }
    })
  }

  const toggleApi = () => {
    setDato('api_enabled', !getDato('api_enabled'))
  }


  const guardar = () => {
    setLoadingApikey(true)
    rootStore.environment.api.accionApi('save', DatosF).then((result: any) => {
      setLoadingApikey(false)
      apiToast(result, 'Cambios guardados!', 'guardar')
    })
  }

  const renderInputField = (label: string, field: string, clase = '', type = 'text', attrs = {}) => {
    return (<div className={clase}>
      <label id={field}>{label}</label>
      <input
        type={type}
        className={'form-control'}
        id={field}
        value={getDato(field)}
        onChange={evt => setDato(field, evt.target.value)}
        {...attrs}
      />
    </div>)
  }

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{display: "flex", height: "100vh", overflowY: "scroll"}}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 ref={topRef} className="text-themecolor">Acceso a API para Proveedores</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item"><a
                  onClick={() => history.push('/admin/')}>Inicio</a></li>
                <li className="breadcrumb-item active">Acceso a API para Proveedores</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  return rootStore.environment.api.fabricantes(page, perPage, order, search, true, {api: true})
                }}
                columns={[
                  {
                    name: 'ID',
                    selector: 'id',
                    sortable: true,
                  },
                  {
                    name: 'Imagen',
                    selector: 'imagen_perfil',
                    sortable: false,
                    cell: (row: any) => row.imagen_perfil ? <img src={row.imagen_perfil}
                                                                 style={{
                                                                   maxWidth: 50,
                                                                   maxHeight: 50,
                                                                   borderRadius: 10,
                                                                   background: '#ededed'
                                                                 }}/> : '',
                  },
                  {
                    name: 'Nombre',
                    selector: 'nombre',
                    sortable: true,
                  },
                  {
                    name: 'API habilitada',
                    selector: 'api_enabled',
                    sortable: true,
                    cell: (row: any) => row.api_enabled ? 'Sí' : 'No',
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>

          {(DatosF !== null) && <div className="card mt-3 mb-0 col-12">
            <div className="card">
              <div className={"card-title mt-3"}>
                <h3 className="text-themecolor">Configuracion de acceso API de proveedor "{getDato('nombre')}"</h3>
              </div>
              <div className="card-body">
                <div className={"form-group"}>
                  <div className={'form-group'}>
                    <label id={'api_key'}>API habilitada</label>
                    <div style={{display: 'flex'}}>
                      <input
                        type={'text'}
                        readOnly={true}
                        className={'form-control'}
                        id={'api_key'}
                        value={getDato('api_enabled') ? "Habilitada" : "No habilitada"}
                        style={{width: '75%'}}
                      />
                      <div style={{flexGrow: 1}}/>
                      <button className={'btn btn-primary'} disabled={LoadingApikey} style={{width: '20%'}}
                              onClick={toggleApi}>
                        {LoadingApikey && <IonSpinner name="crescent"/>}
                        {!LoadingApikey &&
                        <span>{getDato('api_enabled') ? "Deshabilitar" : "Habilitar"} <FIcon.AlertTriangle
                          className={'ml-1'} size={18}/> </span>}
                      </button>
                    </div>
                  </div>

                  <div className={'form-group'}>
                    <label id={'api_key'}>Token de acceso</label>
                    <div style={{display: 'flex'}}>
                      <input
                        type={'text'}
                        readOnly={true}
                        className={'form-control'}
                        id={'api_key'}
                        value={getDato('api_key')}
                        style={{width: '75%'}}
                      />
                      <div style={{flexGrow: 1}}/>
                      <button className={'btn btn-primary ml-2'} disabled={LoadingApikey} style={{width: '8%'}}
                              onClick={verApikey}>
                        {LoadingApikey && <IonSpinner name="crescent"/>}
                        {!LoadingApikey &&
                        <span>Ver<FIcon.Eye className={'ml-1'} size={18}/></span>}
                      </button>
                      <button className={'btn btn-primary ml-2'} disabled={LoadingApikey} style={{width: '11%'}}
                              onClick={regenerarApikey}>
                        {LoadingApikey && <IonSpinner name="crescent"/>}
                        {!LoadingApikey &&
                        <span>Regenerar <FIcon.AlertTriangle className={'ml-1'} size={18}/> </span>}
                      </button>
                    </div>
                  </div>
                  <div className={'form-group'}>
                    <label id={'ip_safelist'}>Lista de ip's habilitadas para el acceso
                      (separadas entre comas)</label>
                    <textarea
                      rows={3}
                      cols={5}
                      className={'form-control'}
                      id={'ip_safelist'}
                      value={getDato('ip_safelist')}
                      onChange={evt => setDato('ip_safelist', evt.target.value)}
                    />
                  </div>
                  {renderInputField('URL de notificaciones de orden nueva', 'pedidos_signal_endpoint', 'form-group', 'url')}

                </div>
                <div className={"form-group text-right"} ref={bottomRef}>
                  <button className={'btn btn-light ml-2'} disabled={LoadingApikey}
                          onClick={() => setDatosF(null)}>
                    {LoadingApikey && <IonSpinner name="crescent"/>}
                    {!LoadingApikey && <span>Cancelar</span>}
                  </button>
                  <button className={'btn btn-primary ml-2'} disabled={LoadingApikey} onClick={guardar}>
                    {LoadingApikey && <IonSpinner name="crescent"/>}
                    {!LoadingApikey && <span>Guardar</span>}
                  </button>
                </div>
              </div>
            </div>
          </div>}
        </div>
      </div>
    </Page>
  );
});

export default AdminApiConfig;
